.quiz {
  width: 100%;
  min-height: 456px;
  background-color: #caffa0;
  border-radius: 24px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 32px;
  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 112%;
    color: #383838;
    margin: 0 0 16px 0;
  }
  &__descr {
    margin: 0 0 20px 0;
    font-size: 16px;
    line-height: 125%;
    color: #383838;
    opacity: 0.7;
    max-width: 400px;
  }
  &__image {
    display: block;
    width: 356px;
    height: 213px;
  }
  &__body {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 576px;
    height: 392px;
    background-color: #fff;
    border-radius: 24px;
    padding: 32px;
    z-index: 5;
  }

  &__block-btn {
    margin: 0;
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__button-first {
    opacity: 0;
    visibility: hidden;
  }
  &__button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: #383838;
    border-radius: 12px;
    width: 108px;
    height: 44px;
    opacity: 0.5;
    span {
      position: relative;
      font-size: 16px;
      color: #fff;
      &::after {
        content: "";
        position: absolute;
        top: 0;
        right: -18px;
        background-image: url(../img/icons/arrow.svg);
        background-size: cover;
        width: 16px;
        height: 16px;
      }
    }
  }
  &__button-prev {
    background-color: #fff;
    border: 1px solid #d8d8d8;
    transition: background-color 0.5s ease;
    opacity: 1;
    span {
      color: #383838;
      transition: color 0.5s ease;
      &::after {
        right: 0;
        left: -18px;
        background-image: url(../img/icons/arrow-prev.svg);
      }
    }
    &:hover {
      background-color: #383838;

      span {
        color: #fff;
        &::after {
          background-image: url(../img/icons/arrow.svg);
          transform: rotate(180deg);
        }
      }
    }
  }
}
.quiz-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.quiz-slide {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.5s;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  &__top {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 24px 0;
  }
  &__title {
    font-weight: 500;
    font-size: 16px;
    line-height: 125%;
    margin: 0;
    color: #383838;
  }
  &__step {
    font-size: 16px;
    line-height: 125%;
    margin: 0;
    color: #9b9b9b;
  }
  &__content {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
  }
  &__radio {
    position: relative;
    width: 252px;
    height: 56px;
    input[type="radio"] {
      display: none;
    }
    input[type="radio"]:checked + label {
      border: 1px solid #383838;
      background-color: #fff;
    }
    input[type="radio"]:checked + label::after {
      border: 6px solid #383838;
    }
  }
  &__radio-big {
    width: 100%;
  }
  &__label {
    display: block;
    width: 100%;
    font-size: 16px;
    line-height: 125%;
    margin: 0;
    color: #383838;
    user-select: none;
    cursor: pointer;
    padding: 18px;
    background-color: #f5f5f5;
    border: 1px solid #d8d8d8;
    border-radius: 16px;
    &::after {
      content: "";
      display: inline-block;
      position: absolute;
      right: 18px;
      bottom: 50%;
      transform: translateY(50%);
      width: 20px;
      height: 20px;
      border: 1px solid #d8d8d8;
      border-radius: 50%;
    }

    // &__radio label:hover:before {
    //   filter: brightness(120%);
    // }
  }
  &__row {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    gap: 8px;
  }
  &__input {
    width: 100%;
    height: 56px;
    border-radius: 16px;
    background-color: #f5f5f5;
    margin: 0;
    border: 1px solid #d8d8d8;
    outline: 0;
    padding: 11px 16px;
    caret-color: #c4c4c4;
    color: #9b9b9b;
  }
}
.active-slide {
  opacity: 1;
  z-index: 2;
}

.button--active {
  opacity: 1;
}
.end-block {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s ease;
  z-index: 10;
  background-color: #fff;
  border-radius: 24px;
  padding: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.end-block--active {
  opacity: 1;
  visibility: visible;
}
.hidden {
  display: none;
}
.hide {
  opacity: 0;
  visibility: hidden;
}
.select-span {
  position: absolute;
  left: 16px;
  top: 54px;
  font-size: 12px;
  line-height: 1;
  color: #9b9b9b;
  margin: 0;
  z-index: 5;
}
.button-send {
  flex-grow: 1;
  width: 100%;
  height: 56px;
  background-color: #383838;
  border-radius: 16px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  padding: 10px 20px;
  margin: 0;
  margin-top: 8px;
}
.quiz-text-button {
  font-size: 12px;
  line-height: 167%;
  color: #9b9b9b;
  margin: 0;
  margin-top: 8px;
  &__link {
    display: inline-block;
    color: #383838;
    border-bottom: 1px solid #383838;
    line-height: 110%;
  }
}
